import React from 'react';
import "./create-game.css"
import EndzoneNav from '../../../components/navigators/endzone-nav/EndzoneNav';

import autoImage from './image/auto-player.jpg'
import manualImage from './image/manual-player.jpg'
import AddIcon from '@mui/icons-material/Add';

import TextField from '@mui/material/TextField';
import { LocalizationProvider } from '@mui/x-date-pickers';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import EndzoneButton from '../../../components/buttons/endzone-new-button/endzone-button'

import { Alert } from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';
import IconButton from '@mui/material/IconButton';

class CreateGame extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            gameDate: "",
            homeTeam: "",
            awayTeam: "",
            alert: {
                style: {display: "none"},
                severity: "info",
                userMessage: ""
            }
        }
        this.httpHandler = require('react-http-client');

    }
    
    displayDiv = (type) => {
        document.getElementById(type + "-img").style.display = "none"
        document.getElementById(type + "-div").style.display = "inline-block"
    }

    createGame = (gameType) => {
        // Validation
        if (this.state.awayTeam.length == 0 || this.state.homeTeam.length == 0)
        {
            this.setState({alert: {                
                style: {display: "block"},
                severity: "error",
                userMessage: "A Home Team and a Away Team are Needed"}}
            )
            window.scrollTo({ top: 0, behavior: 'smooth' });
            return
        }
        else if(this.state.awayTeam === this.state.homeTeam)
        {
            this.setState({alert: {                
                style: {display: "block"},
                severity: "error",
                userMessage: "Away and Home teams cannot be the same, if a scrimmage please add some identifier for the two teams (Ex: Eastview Black, Eastview White)."}}
            )
            window.scrollTo({ top: 0, behavior: 'smooth' });
            return
        }
        else if(this.state.gameDate == "")
        {
            this.setState({alert: {                
                style: {display: "block"},
                severity: "error",
                userMessage: "Please Select a Game Date"}}
            )
            window.scrollTo({ top: 0, behavior: 'smooth' });
            return
        }

        this.httpHandler.post("/endzone/data/game/create", {
            home_team: this.state.homeTeam,
            away_team: this.state.awayTeam,
            game_date: this.state.gameDate.replaceAll("-", "/"),
            game_type: gameType
        }).then((request) => {
            window.location.href = `/endzone/data/game/${gameType}?homeTeam=${this.state.homeTeam}&awayTeam=${this.state.awayTeam}&gameDate=${this.state.gameDate}`
        }, (request) => this.handleError(request))}

        handleError = (request) => {
            try {
                var response = JSON.parse(request.message)
                this.setState({alert: {
                    style: {display: "block"},
                    severity: "error",
                    userMessage: response.data
                }})
            }
            catch {
                this.setState({alert: {
                    style: {display: "block"},
                    severity: "error",
                    userMessage: "Unexpected Error - Please Contact: endzone.support@gmail.con"
                }})
            }
            finally {
                window.scrollTo({ top: 0, behavior: 'smooth' });
            }
        }

    closeAlert = () => {
        let alert = {
            style: {display: "none"},
            severity: "info",
            userMessage: "" 
        }
        this.setState({alert: alert})
    }

    render() {
        return(
            <div className='create-game-page'>
                <div>
                    <EndzoneNav links={[{title: "Return to Data Hub", link: "/endzone/data/hub"},]}></EndzoneNav>
                </div>
                <div className='alert-div' style = {this.state.alert.style}>
                    <Alert variant="filled" severity={this.state.alert.severity}
                            action={
                                <IconButton
                                aria-label="close"
                                color="inherit"
                                size="large"
                                onClick={() => {
                                    this.closeAlert();
                                }}
                                >
                                <CloseIcon fontSize="inherit" />
                                </IconButton>
                            }>
                            {this.state.alert.userMessage}
                    </Alert>
                </div>
                <div className='create-game-body'>

                    <div className='content-div' onClick={() => this.displayDiv("ai")}>
                        <div className='header'>
                            <h3>AI Scouting</h3>
                            <p>Click to create a with Automated Entry</p>
                        </div>
                        <div className='img-div'><img src={autoImage} id = "ai-img"></img></div>
                        <div className='content-create-div' id = "ai-div">
                            <div className='form-div'>
                                <p>Test</p>
                            </div>
                        </div>
                    </div>
                    <div className='content-div' onClick={() => this.displayDiv("manual")}>
                        <div className='header'>
                            <h3>Manual Scouting</h3>
                            <p>Click to create a game with Manual Entry </p>
                        </div>
                        <div className='img-div' id = "manual-img"><img src={manualImage}></img></div>
                        <div className='content-create-div' id = "manual-div">
                            <div className='form-div'>
                                <div className='form-row'>
                                    <h2>New Game</h2>    
                                </div>                                
                                <div className='form-row'>
                                    <div className='form-input'>    
                                        <TextField
                                            id="home-team"
                                            label="Home Team"
                                            size='small'
                                            onChange = {(e) => this.setState({homeTeam: e.target.value})} 
                                        />
                                    </div>
                                </div>
                                <div className='form-row'>
                                    <div className='form-input'>      
                                        <TextField
                                            id="away-team"
                                            label="Away Team"
                                            size='small'
                                            onChange = {(e) => this.setState({awayTeam: e.target.value})} 
                                        />
                                    </div>
                                </div>
                                <div className='divider'></div>
                                <div className='form-row'>
                                    <div className='form-input'>
                                        <LocalizationProvider dateAdapter={AdapterDayjs}>
                                            <DatePicker label="Game Date" format = "DD-MM-YYYY" disableFuture = {true} onAccept={(date) =>  this.setState({gameDate: date.format("MM-DD-YYYY")})}/>
                                        </LocalizationProvider>
                                    </div>
                                </div>
                                <div className='divider'></div>
                                    <div className='form-row'>
                                        <div className='form-button'>
                                            <EndzoneButton onClick = {() => this.createGame("Manual")} text = "Create Game" style = {{"fontSize": "3vh", "padding": "1vh", "margin-top": "2vh"}} icon = {<AddIcon sx = {{"font-size": '150%'}} ></AddIcon>} ></EndzoneButton>
                                        </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}


export default CreateGame;